/* purgecss start ignore */

.MuiFormControl-root {
  width: 100%;
}

.MuiFormControl-root.lc-input {
    display: flex;
    margin-bottom: 24px;
  }

.MuiFormControl-root.lc-input .MuiOutlinedInput-root {
      border-radius: 6px;
    }

.MuiFormControl-root.lc-input input:not([type='color']) {
      border-radius: 6px;
      box-sizing: content-box;
      height: 28px;
      padding-top: 10px;
      padding-bottom: 10px;
      zoom: 1;
    }

.MuiFormControl-root.lc-input-border-white .MuiOutlinedInput-notchedOutline {
        border-color: white;
      }

.MuiFormControl-root.lc-input.dark-mode .MuiFormLabel-root {
        color: white;
      }

.MuiFormControl-root.lc-input.dark-mode .MuiInputBase-input {
        color: white;
      }

.MuiFormControl-root.lc-input.dark-mode .MuiInput-underline:before {
        border-bottom: 1px solid rgba(255, 255, 255, 0.42);
      }

.MuiFormControl-root.lc-input.dark-mode .MuiInput-underline:after {
        border-bottom: 2px solid white;
      }

.MuiFormControl-root.lc-input.dark-mode input:-internal-autofill-selected {
        background-color: rbga(255, 255, 255, 0.42) !important;
      }

.MuiFormControl-root.lc-input.dark-mode .MuiInputAdornment-root *,
      .MuiFormControl-root.lc-input.dark-mode .MuiFormHelperText-root,
      .MuiFormControl-root.lc-input.dark-mode .MuiFormHelperText-root.Mui-error {
        color: white;
      }

.MuiFormControl-root .MuiTypography-body1,
  .MuiFormControl-root .MuiInputBase-root,
  .MuiFormControl-root .MuiInputLabel-root,
  .MuiFormControl-root .MuiFormHelperText-root,
  .MuiFormControl-root .MuiInputAdornment-root {
    font-size: inherit;
  }

.MuiFormControl-root .MuiTypography-body1, .MuiFormControl-root .MuiInputBase-root, .MuiFormControl-root .MuiInputLabel-root, .MuiFormControl-root .MuiFormHelperText-root, .MuiFormControl-root .MuiInputAdornment-root {
    font-family: var(--lc-font-family-sans);
  }

.MuiFormControl-root .MuiTypography-body1, .MuiFormControl-root .MuiInputBase-root, .MuiFormControl-root .MuiInputLabel-root, .MuiFormControl-root .MuiFormHelperText-root, .MuiFormControl-root .MuiInputAdornment-root {
    --tw-text-opacity: 1;
    color: rgba(var(--lc-color-black), var(--tw-text-opacity));
  }

.MuiFormControl-root .MuiInputAdornment-root * {
    --tw-text-opacity: 1;
    color: rgba(var(--lc-color-gray), var(--tw-text-opacity));
  }

.MuiFormControl-root .MuiOutlinedInput-multiline.MuiOutlinedInput-marginDense {
      padding-top: 14px;
      padding-bottom: 15px;
    }

.MuiFormControl-root .MuiOutlinedInput-multiline textarea {
      resize: vertical;
      min-height: 100px;
    }

.MuiFormControl-root .MuiOutlinedInput-notchedOutline {
    --tw-border-opacity: 1;
    border-color: rgba(var(--lc-color-gray-lighter), var(--tw-border-opacity));
  }

.MuiFormControl-root .MuiOutlinedInput-root.Mui-error .MuiOutlinedInput-notchedOutline {
    --tw-border-opacity: 1;
    border-color: rgba(var(--lc-color-danger), var(--tw-border-opacity));
  }

.MuiFormControl-root .MuiInputLabel-outlined.MuiInputLabel-marginDense {
    --tw-text-opacity: 1;
    color: rgba(var(--lc-color-black), var(--tw-text-opacity));
    top: -1px;
    opacity: 0.64;
  }

.MuiFormControl-root .MuiInputLabel-outlined.MuiInputLabel-marginDense:not(.MuiInputLabel-shrink) {
      transform: translate(14px, 17px);
      opacity: 0.44;
    }

.MuiFormControl-root .MuiInputLabel-outlined.MuiInputLabel-marginDense.Mui-disabled, .MuiFormControl-root .MuiOutlinedInput-root.Mui-disabled {
    --tw-text-opacity: 1;
    color: rgba(var(--lc-color-black), var(--tw-text-opacity));
  }

.MuiFormControl-root .MuiInputLabel-outlined.MuiInputLabel-marginDense.Mui-disabled,
  .MuiFormControl-root .MuiOutlinedInput-root.Mui-disabled {
    opacity: 0.35;
    pointer-events: none;
  }

.MuiFormControl-root .MuiInputLabel-outlined.MuiInputLabel-marginDense.Mui-disabled input, .MuiFormControl-root .MuiOutlinedInput-root.Mui-disabled input {
      --tw-bg-opacity: 1;
      background-color: rgba(var(--lc-color-gray-lightest), var(--tw-bg-opacity));
    }

.MuiFormControl-root .lc-input .MuiOutlinedInput-input {
    padding: 16px 14px;
    box-sizing: border-box;
  }

.MuiFormControl-root .MuiInputAdornment-positionEnd, .MuiFormControl-root .MuiInputAdornment-positionStart {
    --tw-text-opacity: 1;
    color: rgba(var(--lc-color-gray), var(--tw-text-opacity));
  }

.MuiFormControl-root .MuiFormHelperText-root {
    --tw-text-opacity: 1;
    color: rgba(var(--lc-color-gray-dark), var(--tw-text-opacity));
    font-size: 0.8em;
    line-height: 1.25;
    font-family: inherit;
    margin-top: 8px;
    letter-spacing: 0;
  }

.MuiFormControl-root .MuiFormHelperText-root.Mui-error {
      --tw-text-opacity: 1;
      color: rgba(var(--lc-color-danger), var(--tw-text-opacity));
    }

.lc-input-label {
  display: block;
  margin-bottom: 8px;
}

.lc-input-label-above input:not([type='color']) {
      background-color: white;
      border-radius: 4px;
    }

.lc-input .MuiOutlinedInput-notchedOutline {
    top: -6px;
    position: absolute !important;
  }

.lc-input .MuiOutlinedInput-root.Mui-focused .MuiOutlinedInput-notchedOutline {
    --tw-border-opacity: 1;
    border-color: rgba(var(--lc-color-primary), var(--tw-border-opacity));
  }

.lc-input .MuiOutlinedInput-root:hover .MuiOutlinedInput-notchedOutline {
    --tw-border-opacity: 1;
    border-color: rgba(var(--lc-color-primary), var(--tw-border-opacity));
  }

.lc-input-select .lc-input .MuiOutlinedInput-root input.MuiAutocomplete-input {
  box-sizing: content-box;
  height: 16px;
}

.MuiAutocomplete-popper {
  font-family: var(--lc-font-family-sans);
}

/* purgecss end ignore */
